
:root{
  --bg:#0f172a; /* slate-900 */
  --card:#111827; /* gray-900 */
  --muted:#1f2937; /* gray-800 */
  --text:#e5e7eb; /* gray-200 */
  --sub:#9ca3af; /* gray-400 */
  --primary:#3b82f6; /* blue-500 */
  --primary-600:#2563eb;
  --danger:#ef4444;
  --success:#10b981;
  --warn:#f59e0b;
  --border:#374151;
}

*{box-sizing:border-box}
html,body,#root{height:100%}
body{margin:0;background:linear-gradient(180deg,#0b1222, #0a0f1a);color:var(--text);font-family:Inter,system-ui,Segoe UI,Roboto,Helvetica,Arial,sans-serif}
.app{
  max-width:1200px;margin:24px auto;padding:16px;
}
.header{
  display:flex;align-items:center;justify-content:space-between;margin-bottom:16px;
}
.title{font-size:22px;font-weight:700;letter-spacing:.3px}
.tabs{display:flex;gap:8px}
.tab{padding:10px 14px;border:1px solid var(--border);border-radius:10px;background:var(--muted);cursor:pointer}
.tab.active{background:var(--primary);border-color:transparent}
.row{display:grid;grid-template-columns:repeat(12,1fr);gap:12px}
.card{
  background:radial-gradient(1000px 600px at 10% 0%, rgba(59,130,246,.15), rgba(59,130,246,0) 45%),
             linear-gradient(180deg, rgba(17,24,39,.95), rgba(17,24,39,.85));
  border:1px solid rgba(59,130,246,.25);
  border-radius:16px;padding:16px;margin-bottom:14px;
  box-shadow:0 10px 40px rgba(2,6,23,.4);
}
.card h3{margin:0 0 12px 0;font-size:15px;color:#cbd5e1}
.grid-2{display:grid;grid-template-columns:1fr 1fr;gap:12px}
.grid-3{display:grid;grid-template-columns:1fr 1fr 1fr;gap:12px}
label{font-size:12px;color:var(--sub);display:block;margin-bottom:6px}
.input, select, textarea{
  width:100%;padding:10px 12px;border-radius:10px;border:1px solid var(--border);
  outline:none;background:#0b1220;color:var(--text);
}
.badge{display:inline-flex;gap:6px;align-items:center;border:1px dashed var(--border);padding:8px 10px;border-radius:999px;font-size:12px;color:#cbd5e1}
.btn{padding:10px 14px;border-radius:12px;border:1px solid var(--border);background:var(--muted);color:var(--text);cursor:pointer}
.btn.primary{background:linear-gradient(180deg,var(--primary),var(--primary-600));border-color:transparent}
.btn.ghost{background:transparent}
.btn.danger{background:linear-gradient(180deg,#ef4444,#dc2626);border-color:transparent}
.btn:disabled{opacity:.6;cursor:not-allowed}
.helper{font-size:12px;color:var(--sub);margin-top:6px}
.hr{height:1px;background:linear-gradient(90deg,transparent,rgba(148,163,184,.4),transparent);margin:10px 0}
.table{width:100%;border-collapse:collapse}
.table th,.table td{border-bottom:1px solid var(--border);padding:10px 8px;text-align:left;font-size:13px}
.kpi{display:flex;gap:10px;flex-wrap:wrap}
.kpi .pill{background:#0b1220;border:1px solid var(--border);padding:8px 12px;border-radius:999px;font-size:12px}
.loading{height:6px;border-radius:999px;background:#0b1220;border:1px solid var(--border);overflow:hidden}
.loading > div{height:100%;width:40%;background:linear-gradient(90deg,transparent,rgba(59,130,246,.6),transparent);animation:load 1.2s infinite}
@keyframes load{0%{transform:translateX(-100%)}100%{transform:translateX(250%)}}
.small{font-size:12px;color:#a3a3a3}
.flex{display:flex;gap:8px;align-items:center}
.right{display:flex;gap:8px;align-items:center;justify-content:flex-end}
